@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;0,600;0,700;1,200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
.notB {
  background: transparent !important;
  transition: background-color 1000ms linear;
}

.btn1 {
  margin-left: 20px;
  width: 40%;
  height: 20%;
  border-radius: 24px;
  font-size: 2rem;
  background: #5eba23a8;
  color: #ffffff;
  border: #00000059 1px solid;
  cursor: pointer;
}

.imgt {
  /* animation: float 6s ease-in-out infinite; */
  width: 100%;
  margin: auto;
  position: absolute;
  top: 0%;
  bottom: 0;
  left: 0;
  right: 0;
}

@-webkit-keyframes float {
  0% {
    /* box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6); */
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }

  50% {
    /* box-shadow: 0 25px 15px 0px rgba(0, 0, 0, 0.2); */
    -webkit-transform: translatey(-20px);
            transform: translatey(-20px);
  }

  100% {
    /* box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6); */
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
}

@keyframes float {
  0% {
    /* box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6); */
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }

  50% {
    /* box-shadow: 0 25px 15px 0px rgba(0, 0, 0, 0.2); */
    -webkit-transform: translatey(-20px);
            transform: translatey(-20px);
  }

  100% {
    /* box-shadow: 0 5px 15px 0px rgba(0, 0, 0, 0.6); */
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
}


.txt {
  text-align: justify;
  padding-left: 10%;
  padding-right: 10%;
}

.backBody {
  background: url(https://www.fiatc.es/_cache_a257/_themesdelivery/staticblog/assets/img/uploads/2018/01/pasos-a-seguir-si-te-pierdes-en-el-bosque.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
