.color-picker__input {
    display: none;
}
.color-picker__input:checked + .color-picker__color:after {
    content: '';
}
.color-picker__input:disabled + .color-picker__color {
    opacity: 0.5;
}
.color-picker__input:disabled + .color-picker__color:hover {
    cursor: not-allowed;
}
.color-picker__color {
    position: relative;
    display: block;
    width: 18px;
    height: 18px;
}
.color-picker__color:hover {
    cursor: pointer;
}
.color-picker__color:after {
    pointer-events: none;
    position: absolute;
    top: -2px;
    left: -2px;
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    outline: 2px solid black;
    content: none;
}
.color-picker__color--black {
