.animated-circle {
    background-color: transparent;
    border: none;
    height: 40px;
    width: 40px;
    cursor: pointer;
    position: absolute;
}

.animated-circle:after {
    content: "";
    height: 25px;
    width: 25px;
    background-color: #dabd1d;
    border-radius: 50%;
    position: absolute;
    top: 5px;
    left: 10px;
    box-shadow: 0 0 0 0 rgba(217, 190, 38, 0.4);
    -webkit-animation: ripple 2s infinite;
    animation: ripple 2s infinite;
}

.fixed {
