@-webkit-keyframes ripple {
    0% {
        -webkit-transform: scale(0.8);
        transform: scale(0.8);
    }
    70% {
        -webkit-transform: scale(1.3);
        transform: scale(1.3);
        box-shadow: 0 0 0 20px rgba(0, 159, 153, 0);
    }
    to {
        -webkit-transform: scale(0.8);
        transform: scale(0.8);
        box-shadow: 0 0 0 0 rgba(0, 159, 153, 0);
    }
}

@keyframes ripple {
    0% {
        -webkit-transform: scale(0.8);
        transform: scale(0.7);
