        width: 100%;
        margin-left: 0;
        padding-top: 20%;
    }
    .contact-sec .contact-details {
        width: 100%;
        margin-left: 0;
        margin-top: 80px;
    }
    .contact-sec .contact-details ul li:first-child {
        width: 100%;
    }
    .contact-sec .contact-details ul li {
        display: block;
        margin-bottom: 30px;
    }
    .contact-sec .contact-details ul li i{
        margin-right: 0;
        margin-bottom: 15px;
        display: block;
    }
    .contact-social ul {
        justify-content: center;
    }

    /* Modal Window */
    .shoes-list {
        padding: 50px 0;
    }
    .morphic-title p {
        margin-top: 30px;
        margin-bottom: 30px;
    }
    .modal-btn .btn.btn-medium {
        font-size: 12px;
    }
    .morphic-title h3 , .price-modal h1 {
        font-size: 36px;
    }
    .morphic-title p {
        font-size: 14px;
    }
    .carousel-indicators {
        margin-bottom: 10% !important;
    }

    /* Background Images Adjustment */
    .slider-area {
        background-position-x: -37%;
    }
    .all-products {
        background-position-x: -12%;
    }
    .reviews {
        background-position-x: -23%;
    }
    .contact-sec {
        background-position-x: -6%;
    }
}

@media (max-width: 575.98px) {
    .h1, h1 {
        font-size: 30px;
    }
    p {
        font-size: 12px;
        line-height: 18px;
    }
    .btn.btn-medium {
        font-size: 12px;
    }
    /* Banner */
    .slider-area h1 {
        font-size: 32px;
    }
    .slider-area p {
        font-size: 12px;
    }
}
